import Modal from './../_modal';
import template from './selectTopicModal.jade';
import './selectTopicModal.scss';

import topicTemplate from './topicItem.jade';

const TopicItemView = MarionetteEpoxy.View.extend({
    tagName: "li",
    className: 'collection-item',
    template: topicTemplate,
    events: {
        'click': 'onClickEl'
    },
    onClickEl() {
        this.trigger('select:item', this.model);
    }

});

const TopicCollectionView = Marionette.CollectionView.extend({
    tagName: 'ul',
    className: 'collection',
    childView: TopicItemView,
    childEvents: {
        'select:item': 'onSelectTopic'
    },
    onSelectTopic(view, topic) {
        this.trigger('select:item', topic);
    }
});

export default Modal.extend({
    template: template,
    className: 'select-topic-modal',

    regions: {
        topicContainer: '[data-js-topic-container]',
    },

    initialize() {
        this.topicCollectionView = new TopicCollectionView({
            collection: common.topicCollection
        })
        this.listenTo(this.topicCollectionView, 'select:item', this.onSelectTopic);
    },
    onRender() {
        this.topicContainer.show(this.topicCollectionView);
    },
    onSelectTopic(topic) {
        this.ok(topic);
    },


})



