// import common from 'commonObj';
import './_modal.scss';

export default Marionette.LayoutView.extend({
    showModal(modal = false) {
        $(':focus').blur();
        if(!modal) {
            this.$el.click((e)=> {
                if($(e.target).hasClass('table-middle-cell')) {
                    this.cancel();
                }
            });
        }
        return  this.showAsync = common.appController.showModal(this);
    },
    ok(data) {
        if(!this.showAsync) { throw new Error('Modal not show') }
        this.showAsync.resolve(data);
    },
    cancel(data) {
        if(!this.showAsync) { throw new Error('Modal not show') }
        this.showAsync.reject(data);
    },
});



