import template from './searchUserPage.jade';
import rivalTemplate from './rivalItem.jade';
import './searchUserPage.scss';
import InfoModal from 'modals/infoModal/infoModal';

import rivalCollection from 'collections/rivalCollection';

const RivalItemView = MarionetteEpoxy.View.extend({
    template: rivalTemplate,
    className: 'width-100 waves-effect',
    ui: {
        addFriend: '[data-js-add-friend]'
    },
    events: {
        'click @ui.addFriend': 'onClickAddFriend',
        'click': 'onClickItem',
    },
    bindings: {
        '@ui.addFriend': 'classes: {hide: is_friend}'
    },
    initialize() {
        this.epoxify();
    },

    onClickAddFriend(e) {
        e.stopPropagation();
        common.api.addFriend(this.model.get('user_id'))
            .done((data)=> {
                this.model.set({is_friend: "1"});
                (new InfoModal({message: `Пользователь ${this.model.get('u_login')} добавлен в друзья`})).showModal();
            })
    },
    onClickItem() {
        this.trigger('select:rival', this.model);
    }
});

const RivalCollectionView = Marionette.CollectionView.extend({
    className: '',
    childView: RivalItemView,
    childEvents: {
        'select:rival': 'onSelectRival'
    },
    onSelectRival(view, rival) {
        this.trigger('select:rival', rival);
    }
});

export default MarionetteEpoxy.LayoutView.extend({
    template: template,
    className: 'search-user-page page',

    ui: {
        searchStr: '[data-js-user-name]'
    },
    events: {
        'keyup @ui.searchStr': 'onChangeSearch',
    },
    bindings: {},

    regions: {
        'resultContainer': '[data-js-rival-list]',
    },

    initialize() {
        this.rivalCollection = new rivalCollection();
        this.rivalCollectionView = new RivalCollectionView({
            collection: this.rivalCollection
        });
        this.listenTo(this.rivalCollectionView, 'select:rival', this.onSelectRival);
        this.debounceChangeSearch = _.debounce(this.changeSearch.bind(this), 1000);
        this.epoxify()
    },
    onRender() {
        this.resultContainer.show(this.rivalCollectionView);
    },
    onChangeSearch(e) {
        this.debounceChangeSearch($(e.currentTarget).val());
    },
    changeSearch(str) {
        this.rivalCollection.setSearch(str);
    },
    onSelectRival(rival) {
        common.appController.startGame(rival.get('user_id'));
    }

});



