import serverApi from './serverAPI';
import validateModel from 'models/validateModel';
import UserModel from 'models/userModel';
import TopicCollection from 'collections/topicCollection'
import UserGameCollection from 'collections/userGameCollection';


export const ValidateModel = validateModel;
export const api = serverApi;
export const emailRegexp = /^[-a-z0-9~!$%^&*_=+}{\'?]+(\.[-a-z0-9~!$%^&*_=+}{\'?]+)*@([a-z0-9_][-a-z0-9_]*(\.[-a-z0-9_]+)*\.(aero|arpa|biz|com|coop|edu|gov|info|int|mil|museum|name|net|org|pro|travel|mobi|[a-z][a-z])|([0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}))(:[0-9]{1,5})?$/i;
export const user = new UserModel();
export const topicCollection = new TopicCollection();
export const userGameCollection = new UserGameCollection();



