import template from './AppLayout.jade';

var pageRegion = Marionette.Region.extend({
    el: '#page-container',
    attachHtml: function(view) {
        this.el.appendChild(view.el);
    }
});
var modalRegion = Marionette.Region.extend({
    el: '#modal-container',
    attachHtml: function(view) {
        this.el.appendChild(view.el);
    }
});

export default Marionette.LayoutView.extend({
	template: template,

	initialize() {
	},

	regions: {
		header: '#header-container',
        pageRegion: pageRegion,
		footer: '#footer-container',
        modalRegion: modalRegion
	},

    showModal(modal) {
        const async = $.Deferred();
        this.modalRegion.show(modal);
        const $el = $('.modal-overflow', modal.$el);
        $el.width();
        $el.addClass('show');
        async.always(()=> {
            $el.removeClass('show');
            setTimeout(()=> {
                modal.destroy();
            }, 300)
        })
        return async;
    },

	showPage(pageConstructor, options={}) {
		var curView = this.pageRegion.currentView;
		var newView = new pageConstructor(options);
		if(curView) {
			curView.$el.removeClass('show');
			setTimeout(()=> {
				curView.destroy();
			}, 500);
		}
        this.pageRegion.show(newView, { preventDestroy: true });
		newView.$el.width();
		newView.$el.addClass('show');
	}
});



