import template from './restorePasswordPage.jade';
import './restorePasswordPage.scss';

import infoModal from 'modals/infoModal/infoModal'
import loadingModal from 'modals/loadingModal/loadingModal'
// import common from 'commonObj';

export default MarionetteEpoxy.View.extend({
  template: template,
  className: 'restore-password-page page',

  ui: {
    form: 'form',
    email: '[data-js-email]',
    buttonRestore: '[data-js-restore]',
  },

  bindings: {
  },

  events: {
    'click @ui.buttonRestore': 'onClickRestore',
    'submit @ui.form': 'onFormSubmit'
  },

  initialize() {
    this.model = new common.ValidateModel();
    this.model.bindingView(this, {
      '@ui.email': 'email',
    })
    this.epoxify();
  },

  onClickRestore(e) {
    e.preventDefault();
    e.stopPropagation();
    this.ui.form.submit();
  },
    onFormSubmit(e) {
        e.preventDefault();
        if(this.model.validate()) {
            let loadingDfd = new loadingModal({message:'loading...'}).showModal();
            common.api.restorePassword(this.model.get('email'))
                .done(()=> {
                    loadingDfd.resolve();
                    (new infoModal({message: `Письмо с инструкциями по востановлению пароля отправлено на: ${this.model.get('email')}`})).showModal()
                    .always(()=> {
                        common.router.navigate('', {trigger: true});
                    })
                })
                .fail(function(err) {
                    loadingDfd.reject();
                    (new infoModal({message: err.text})).showModal();
                })
        }

    },
})



