import AppController from './AppController';
import AppRouter from './AppRouter';

import 'material-design-icons/iconfont/material-icons.css'
import 'materialize-css/sass/materialize.scss';
import 'materialize-css/dist/js/materialize.js'


import './updateBackbone';
import './../css/main.scss';

import infoModal from 'modals/infoModal/infoModal'

const app = new Marionette.Application();

const appController = new AppController();

const appRouter = new AppRouter({
	controller: appController
})

common.router = appRouter;
common.appController = appController;

// for development
window.user = common.user
window.topics = common.topicCollection;


app.addInitializer(function(options){
    document.addEventListener("deviceready", onDeviceReady, false);
    function onDeviceReady() {
        if (window.cordova.platformId == "browser") {
            facebookConnectPlugin.browserInit("1761962527353026");
        } else {
            var push = PushNotification.init({
                android: {
                    senderID: "555817085435"
                },
                ios: {
                    alert: "true",
                    badge: "true",
                    sound: "true"
                },
                windows: {}
            });
            
            push.on('registration', function(data) {
                console.log(data);
            });

            push.on('notification', function(data) {
                console.log(data);
                (new infoModal({message: "Push notification: " + JSON.stringify(data)})).showModal();
            });

            push.on('error', function(error) {
                console.log(error);
                (new infoModal({message: "Push notifications error: " + JSON.stringify(error)})).showModal();
            });
            
            SocialVk.init("5509617", 
                function(result){
                    console.log(result);
                    console.log(SocialVk);
                }, 
                function(error){
                    console.log(error);
                    (new infoModal({message: "SocialVk init error: " + JSON.stringify(error)})).showModal();
                }
            );
        }
    }
});

app.on("start", function(){
  common.user.init();
  Backbone.history.start({ pushState: false });
});

app.start();



