import infoModal from 'modals/infoModal/infoModal';

const serverURL = 'https://pdd.demetra.media/server.php';

function ajaxController(data) {
    let async = $.Deferred();
    if (!data.v_android) {
        data.v_android = 9;
    }
    if (common.user.get('auth')) {
        data[common.user.getSessionKey()] = common.user.getSessionValue();
    }
    if (common.user.get('socialAuth')) {
        if (common.user.get('socialAuth') === 'vk') {
            data['vk_user_id'] = common.user.get('vkId');
            data['access_token'] = common.user.get('vkToken');
            data['from'] = 'vk';
        } else if (common.user.get('socialAuth') === 'fb') {
            data['fb_user_id'] = common.user.get('fbId');
            data['access_token'] = common.user.get('fbToken');
            data['from'] = 'fb';
        }
    }
    $.ajax({
        url: serverURL,
        dataType: 'jsonp',
        data: data,
    })
        .done(function (data) {
            if (data.answer_type == 'err') {
                async.reject({
                    code: data.answer.error_code,
                    text: data.answer.error_text
                });
                return;
            }
            async.resolve(data);
        })
        .fail(function () {
            (new infoModal({message: 'Что то пошло не так'})).showModal();
        })
    return async.promise();
}

export default {

    registration(login, email, password) {
        return ajaxController({
            action: 'reg_user',
            email: email,
            login: login,
            password: password,
        });
    },
    signIn(login, password) {
        return ajaxController({
            action: 'get_user_info',
            login: login,
            password: password,
        });
    },
    signInVk(name, surname) {
        return ajaxController({
            action: 'get_user_info',
            name: name,
            surname: surname,
        });
    },
    signInFb(name, surname) {
        return ajaxController({
            action: 'get_user_info',
            name: name,
            surname: surname,
        });
    },
    signInGuest() {
        return ajaxController({
            action: 'reg_guest'
        });
    },
    restorePassword(email) {
        return ajaxController({
            action: 'password_recovery',
            email: email,
        });
    },
    logout() {
        return ajaxController({
            action: 'logout',
        });
    },


    getUserGames() {
        return ajaxController({
            action: 'get_user_games',
        });
    },
    getUserInfo() {
        return ajaxController({
            action: 'get_user_info',
        });
    },
    getUsersByLogin(login, page) {
        return ajaxController({
            action: 'get_app_users_by_str',
            login: login,
            page: page,
        });
    },
    addFriend(userId) {
        return ajaxController({
            action: 'add_friend',
            user_id: userId,
        });
    },
    getFriends() {
        return ajaxController({
            action: 'get_friends',
        });
    },
    checkRandomGame() {
        return ajaxController({
            action: 'start_game',
        });
    },

    createRandomRoom(topicId) {
        return ajaxController({
            action: 'create_room',
            topic_id: topicId
        });
    },

    startGameById(opponentId) {
        return ajaxController({
            action: 'start_game',
            opponent_user_id: opponentId,
        });
    },

    getInfoByRoom(roomId) {
        return ajaxController({
            action: 'get_info_by_room',
            room_id : roomId,
        });
    },
}



