import template from './registrationPage.jade';
import './registrationPage.scss';

import infoModal from 'modals/infoModal/infoModal'
import loadingModal from 'modals/loadingModal/loadingModal'
// import common from 'commonObj';

export default MarionetteEpoxy.View.extend({
    template: template,
    className: 'registration-page page',

    ui: {
        buttonRegistration: '[data-js-registration]',
        form: 'form',
        login: '[data-js-login]',
        email: '[data-js-email]',
        password: '[data-js-password]',
        passwordRepeat: '[data-js-password-repeat]',
    },

    bindings: {},

    events: {
        'click @ui.buttonRegistration': 'onClickRegistration',
        'submit @ui.form': 'onFormSubmit'
    },

    initialize() {
        this.model = new common.ValidateModel();
        this.model.bindingView(this, {
            '@ui.login': 'login',
            '@ui.email': 'email',
            '@ui.password': 'password',
            '@ui.passwordRepeat': 'passwordRepeat',
        })
        this.epoxify();
    },

    onClickRegistration(e) {
        e.preventDefault();
        e.stopPropagation();
        this.ui.form.submit();
    },
    onFormSubmit(e) {
        e.preventDefault();
        if (this.model.validate()) {
            let loadingDfd = new loadingModal({message: 'loading...'}).showModal();
            common.api.registration(
                this.model.get('login'),
                this.model.get('email'),
                this.model.get('password'))
                .done((data)=> {
                    loadingDfd.resolve();
                    common.user.parseAnswer(data);
                    common.router.navigate('index', {trigger: true});
                })
                .fail(function (err) {
                    loadingDfd.reject();
                    (new infoModal({message: err.text})).showModal();
                });
        }
    }

})



