import userGameModel from 'models/userGameModel';

export default Backbone.Collection.extend({
    model: userGameModel,

    parse(data) {
        if (data && data.answer && data.answer.user_games) {
            this.reset(_.map(data.answer.user_games, function (value) {
                return value;
            }));
        } else {
            this.reset([]);
        }
    }
})



