export default Epoxy.Model.extend({
  defaults: {
    login: '',
    loginClass: false,
    loginAction: false,
    email: '',
    emailClass: false,
    emailAction: false,
    password: '',
    passwordClass: false,
    passwordAction: false,
    passwordRepeat: '',
    passwordRepeatClass: false,
    passwordRepeatAction: false,
  },

  computeds: {
    loginInvalid() {
      let loginLength = this.get('login').length;
      return !(loginLength >= 4 && loginLength <= 30);
    },
    emailInvalid() {
      return !common.emailRegexp.test(this.get('email'));
    },
    passwordInvalid() {
      return !(this.get('password').length > 6);
    },
    passwordRepeatInvalid() {
      return this.get('password') != this.get('passwordRepeat');
    }
  },

  bindingView(view, fields) {
    this.keys = [];
    this.view = view;
    _.each(fields, (value, key)=> {
      let fixValue = value;
      if(!this.has(fixValue)) {
        console.log(fixValue + ' in not validate field');
        return;
      }
      this.keys.push(fixValue);
      view.bindings[key] = `value:${fixValue}, classes:{animateInvalid:${fixValue}Action,invalid:${fixValue}Class}`;
      view.events[`focus ${key}`] = (e)=> {
        this.set(`${fixValue}Class`, false);
      };
      view.events[`focusout ${key}`] = (e)=> {
        this.set(`${fixValue}Class`, this.get(`${fixValue}Invalid`));
      }
    })
    view.delegateEvents();
  },

  validate() {
    var result = true;
    _.each(this.keys, (key)=> {
      if(this.get(`${key}Invalid`)) {
        this.set(`${key}Class`, true);
        this.set(`${key}Action`, false);
        this.view.$el.width();
        this.set(`${key}Action`, true)
        result = false;
      }
    })

    return result;
  },

});



