import template from './signinPage.jade';
import './signinPage.scss';

import infoModal from 'modals/infoModal/infoModal'
import loadingModal from 'modals/loadingModal/loadingModal'
// import common from 'commonObj';

export default MarionetteEpoxy.View.extend({
  template: template,
  className: 'signin-page page',

  ui: {
    restorePassword: '[data-js-restore-password]',
    buttonSignin: '[data-js-signin]',
    buttonGuest: '[data-js-guest]',
    login: '[data-js-login]',
    password: '[data-js-password]',
    form: 'form',
  },

  events: {
    'click @ui.restorePassword': 'onClickRestorePassword',
    'click @ui.buttonSignin': 'onClickSignin',
    'submit @ui.form': 'onFormSubmit',
      'click @ui.buttonGuest': 'onClickGuest',
  },

  onClickRestorePassword() {
    common.router.navigate('restorePassword', {trigger: true});
  },

  onClickSignin(e) {
    e.preventDefault();
    e.stopPropagation();
    this.ui.form.submit();
  },
    onFormSubmit(e) {
        let loadingDfd = new loadingModal({message:'loading...'}).showModal();
        e.preventDefault();
        common.api.signIn(this.ui.login.val(), this.ui.password.val())
            .done((data)=> {
                loadingDfd.resolve();
                common.user.parseAnswer(data);
                common.router.navigate('index', {trigger: true});
            })
            .fail(function(err) {
                loadingDfd.reject();
                (new infoModal({message: err.text})).showModal();
            });
    },
    onClickGuest() {
        let loadingDfd = new loadingModal({message:'loading...'}).showModal();
        common.api.signInGuest()
            .done((data)=> {
                loadingDfd.resolve();
                common.user.parseAnswer(data);
                common.router.navigate('index', {trigger: true});
            })
            .fail(function(err) {
                loadingDfd.reject();
                (new infoModal({message: err.text})).showModal();
            });
    },
})



