export default Epoxy.Model.extend({
  defaults: {
    auth: false,
    sessionKey: '',
    sessionValue: '',
    name: '',
    id: '',
    rate: '',
	socialAuth: false,
	vkId: '',
	vkToken: '',
	fbId: '',
	fbToken: ''
  },
  initialize() {

  },
  
  init() {
    this.getState();
    this.listenTo(this, 'change', this.saveState);
  },

  getState() {
    if(window.localStorage) {
      this.set(JSON.parse(window.localStorage.getItem('userdata')));
    }
  },

  saveState() {
    if(window.localStorage) {
      window.localStorage.setItem('userdata', JSON.stringify(this.toJSON()));
    }
  },
  getSessionKey() {
    return this.get('sessionKey');
  },
  getSessionValue() {
    return this.get('sessionValue');
  },
  setVkIdToken(id, token) {
	this.set({
		vkId: id,
		vkToken: token,
		socialAuth: 'vk'
	});
	
	return this;
  },
  setFbIdToken(id, token) {
	this.set({
		fbId: id,
		fbToken: token,
		socialAuth: 'fb',
	});
	return this;
  },
  parseAnswer(data) {
    this.set({
      name: data.answer.u_login,
      id: data.answer.u_id,
      rate: data.answer.u_rate,
      sessionKey: data.session_name,
      sessionValue: data.session_id,
      auth: true,
    });
  },
  logout() {
    common.api.logout();
    this.set(this.defaults);
    common.router.navigate('', {trigger: true});
  },
});



