import topicModel from 'models/topicModel';

export default Backbone.Collection.extend({
    model: topicModel,

    parse(data) {
        if (data && data.answer && data.answer.topics) {
            this.reset(_.map(data.answer.topics, function (value) {
                return value;
            }));
        } else {
            this.reset([]);
        }
    }
})



