import Epoxy from 'backbone.epoxy';

const epoxyMethods = {
  epoxify: function() {
    Epoxy.View.mixin(this);
    var binding = {};
    this.bindings = _.each(this.bindings, (value, key)=> {
      let keyUi = key.split('@ui.');
      if(keyUi[1]) {
        binding[this.ui[keyUi[1]]] = value;
        return;
      }
      binding[key] = value;
    })
    this.bindings = binding;
    this.listenTo(this, "ui:bind", this.applyBindings);
    this.listenTo(this, "before:close", this.removeBindings);
  },
  // Override Marionette's impl so we can trigger our own event
  bindUIElements : function() {
    this.trigger("ui:bind");
    Marionette.View.prototype.bindUIElements.apply(this, arguments);
  }
}

export const View = Marionette.ItemView.extend(epoxyMethods);
export const LayoutView = Marionette.LayoutView.extend(epoxyMethods);



