import template from './indexPage.jade';
import './indexPage.scss';

import userGameTemplate from './userGameItem.jade';



const UserGameItemView = MarionetteEpoxy.View.extend({
    template: userGameTemplate,
    className: 'width-100 waves-effect',
    ui: {},
    events: {
        'click': 'onClickItem',
    },
    bindings: {},
    initialize() {
        console.dir(this.model.attributes);
        this.epoxify();
    },
    onClickItem() {
        this.trigger('click:game', this.model);
    }
});

const UserGameCollectionView = Marionette.CollectionView.extend({
    className: '',
    childView: UserGameItemView,
    childEvents: {
        'click:game': 'onClickGame'
    },
    onClickGame(view, game) {
        this.trigger('click:game', game);
    }
});

export default MarionetteEpoxy.LayoutView.extend({
    template: template,
    className: 'index-page page',

    ui: {
        'buttonLogout': '[data-js-logout]',
        'buttonSettings': '[data-js-settings]',
        'buttonNewGame': '[data-js-new-game]',
        'userName': '[data-js-user-name]',
    },
    events: {
        'click @ui.buttonLogout': 'onClickLogout',
        'click @ui.buttonSettings': 'onClickButtonSettings',
        'click @ui.buttonNewGame': 'onClickNewGame',
    },
    bindings: {
        '@ui.userName': 'text: name',
    },

    regions: {
        gameList: '[data-js-user-game-list]',
    },

    initialize() {
        this.model = common.user;
        this.userGameCollectionView = new UserGameCollectionView({
            collection: common.userGameCollection
        });
        this.listenTo(this.userGameCollectionView, 'click:game', this.onClickGame);
        this.epoxify()
    },
    onRender() {
        this.gameList.show(this.userGameCollectionView);
    },

    onClickGame(game) {
        common.api.getInfoByRoom(game.get('room_id'))
            .done(function(data) {
                console.dir(data);
                common.router.navigate('game', {trigger: true});
            })
    },

    onClickLogout() {
        common.user.logout();
    },
    onClickButtonSettings() {
        common.router.navigate('settings', {trigger: true});
    },
    onClickNewGame() {
        common.router.navigate('newgame', {trigger: true});
    },
});



