import AppLayout from './layouts/AppLayout';

import './pages/pages.scss';

import selectTopicModal from 'modals/selectTopicModal/selectTopicModal';
import infoModal from 'modals/infoModal/infoModal';

import indexPage from './pages/indexPage/indexPage';
import settingsPage from './pages/settingsPage/settingsPage';
import newGamePage from './pages/newGamePage/newGamePage';
import searchUserPage from './pages/searchUserPage/searchUserPage';
import last20gamesPage from './pages/last20gamesPage/last20gamesPage';
import gamePage from './pages/gamePage/gamePage';

import authPage from './pages/authPage/authPage';
import registrationPage from './pages/registrationPage/registrationPage';
import signinPage from './pages/signinPage/signinPage';
import restorePasswordPage from './pages/restorePasswordPage/restorePasswordPage';


export default Marionette.Object.extend({
    initialize() {
        this.appLayout = new AppLayout();
        this.listenTo(common.user, 'change:auth', this.onChangeAuth);
    },
    
    startGame(rival_id = null) {
        let apiMethod = 'checkRandomGame';
        if(rival_id) { apiMethod = 'startGameById' }
        common.api[apiMethod](rival_id)
            .done(function (data) {
                console.log(data)
                common.router.navigate('game', {trigger: true});
            })
            .fail(function (data) {
                if(data.code == "00017") { //Выберите тему
                    (new selectTopicModal()).showModal()
                        .done(function (topic) {
                            common.api.createRandomRoom(topic.get('topic_id'))
                                .done(function(data) {
                                    console.log(data);
                                    common.router.navigate('game', {trigger: true});
                                })
                                .fail(function(data) {
                                    (new infoModal({message: data.text})).showModal();
                                })
                        })
                } else {
                    (new infoModal({message: data.text})).showModal();
                }

            })
    },
    
    showModal(modal) {
        return this.appLayout.showModal(modal);
    },
    showOutsidePage(pageConstructor) {
        if (!common.user.get('auth')) {
            this.appLayout.showPage(pageConstructor);
            return;
        }
        common.router.navigate('index', {trigger: true});
    },
    showInsidePage(pageConstructor) {
        if (common.user.get('auth')) {
            this.appLayout.showPage(pageConstructor);
            return;
        }
        common.router.navigate('', {trigger: true});
    },
    onChangeAuth(user, auth) {
        if (!auth) {
            common.topicCollection.reset();
            return;
        }
        common.api.getUserGames()
            .done((data)=> {
                common.topicCollection.parse(data);
                common.userGameCollection.parse(data);
            })
    },


    onShowIndex() {
        this.showInsidePage(indexPage);
    },
    onShowSettings() {
        this.showInsidePage(settingsPage);
    },
    onShowNewGame() {
        this.showInsidePage(newGamePage);
    },
    onShowSearchUser() {
        this.showInsidePage(searchUserPage);
    },
    onShowLast20Games() {
        this.showInsidePage(last20gamesPage);
    },
    onShowGamePage(){
        this.showInsidePage(gamePage);
    },

    onShowRestorePassword() {
        this.showOutsidePage(restorePasswordPage);
    },
    onShowAuth() {
        this.showOutsidePage(authPage);
    },
    onShowRegistration() {
        this.showOutsidePage(registrationPage);
    },
    onShowSignin() {
        this.showOutsidePage(signinPage);
    },

});



