import rivalModel from 'models/rivalModel';

export default Backbone.Collection.extend({
    model: rivalModel,

    initialize() {
        this.search = '';
        this.load = false;
    },

    setSearch(str) {
        this.search = str;
        this.loadResult()
    },
    loadResult() {
        this.page = 0;
        this.addData();
    },
    addData() {
        if (!this.load) {
            this.load = true;
            common.api.getUsersByLogin(this.search, this.page)
                .done((data)=> {
                    this.parse(data);
                })
                .fail(()=> {
                    this.reset();
                })
                .always(()=> {
                    this.load = false;
                });
        }
    },
    parse(data) {
        this.reset(data.answer);
    },
});



