import buyModal from 'modals/buyModal/buyModal'

import template from './settingsPage.jade';
import './settingsPage.scss';

export default MarionetteEpoxy.View.extend({
  template: template,
  className: 'settings-page page',

  ui: {
    'buyButton': '[data-js-buy]',
  },
  events: {
    'click @ui.buyButton': 'onClickBuy',
  },
  bindings: {

  },

  onClickBuy() {
    (new buyModal()).showModal();
  },
});


